.side-bar {
    width: 85px;
    // position: absolute;
    // background-color: #1E2071;
    // top: 0;
    // left: 0;
    // bottom: 0;
}

.main {
    width: 100%;
    padding-left: 85px;
}

.customer-care-main {
    font-family: Usual;
    color: #151C1B;
    font-size: 12px;
    min-width: 1000px;
    
    .member-info {
        position: relative;
        background-color: #F5F7FB;
        width: 22%;
        
        .bread-crumbs {
            font-size: 9px;
            font-weight: 300;
            color: #272E4C;
            padding-bottom: 5px;

            .current-text {
                color: #151C1B;
                font-weight: 600;
            }
        }

        .circle {
            padding: 0.25rem 0.25rem;
            margin-right: 8px;
            display: inline-block;
            border-radius: 50%;
        }

        .tag {
            border: #FAB435 solid 3px;
            border-radius: 3px;
            margin: -10px;
            padding: 7px;
            position: relative;

            .tag-label {
                background: #FAB435;
                border-radius: 5px;
                padding: 3px 5px;
                color: #FFFFFF;
                position: absolute;
                margin-top: -3px;
                margin-right: -3px;
                right: 0;
                top: 0;
            }
        }
    }

    .ticket-info {
        position: relative;
        width: 27%;
        padding: 0 1rem;
    }

    .availment-section {
        position: relative;
        width: 51%;
        padding: 0 1rem 0 0;
    }

    .confinement-details {
        position: relative;
        width: 51%;
        padding: 0 1rem 0 0;
    }

    .update-ticket {
        position: relative;
        width: 78%;
    }

    .bordered-section {
        border: 1px solid #9EA3AC;
        border-radius: 3px;
        padding: 1.5rem 1rem 1.5rem 1rem;
    }

    .section {
        padding: 1.5rem 1rem 1.5rem 1rem;
    }

    .small-section {
        padding: 0.75rem;
    }

    .sub-section {
        padding: 16px 0;
    }

    .sub-section-2 {
        padding: 0.5rem 0;
    }

    // .header {
    //     font-size: 20px;
    //     font-weight: 700;
    //     margin: 5px 0px 10px 0px;
    // }

    .header-1 {
        font-size: 16px;
        font-weight: 600;
        margin: 5px 0px 10px 0px;
    }

    .header-2 {
        font-weight: 600;
        margin-bottom: 5px;
    }

    .header-3 {
        font-size: 11px;
        font-weight: 600;
        margin-bottom: 12px;
    }

    .text {
        margin-bottom: 5px;
    }

    .small-text {
        font-size: 11px;
        margin-bottom: 5px;
    }

    .smaller-text {
        font-size: 9px;
        margin-bottom: 5px;
    }

    .light-text {
        color: #272E4C;
        font-weight: 200;
        font-size: 11px;
        margin-bottom: 3px;
        opacity: 0.5;
    }
}






.line-border {
    background: #151C1B;
    opacity: 0.1;
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
}

.custom-table-small {
    .MuiTableCell-head {
        text-align: center;
        font-weight: 600;
        color: #000000;
        padding: 10px !important;
    }
    .MuiTableCell-body {
        padding: 5px !important;
    }
}
.custom-pagination-small {
    .MuiTablePagination-toolbar {
        padding: 0;
    }
    .MuiTablePagination-select {
        padding-left: 0;
    }
    .MuiTablePagination-selectRoot {
        margin: 0 10px;
    }
    .MuiTablePagination-actions {
        margin-left: 0;
    }
    .MuiIconButton-root {
        padding: 5px;
    }
}

.custom-table {
    .MuiTableCell-head {
        font-size: 14px !important;
        font-weight: 600;
        color: #000000;
        background-color: #DDDFE3;
        padding: 15px !important;
    }
}

.custom-table-scrollable {
    margin: 5px 0 5px 0;
    box-shadow: 0px 3px 6px #00000029;
    overflow-x: scroll;
    overflow-y: hidden;
    .MuiTable-root {
        width: 100%;
        padding: 5px 0 5px 0;
    }
    .MuiTableCell-root {
        border-bottom: none;
    }
    .MuiTableCell-head {
        color: #000000;
        background-color: #DDDFE3;
        padding: 10px 0 5px 10px;
        line-height: 1;
    }
    .MuiTableCell-body {
        font-size: inherit;
        padding: 15px 0 15px 15px;
        line-height: 1;
    }
    .MuiTableCell-root:last-child {
        padding-right: 10px;
    }
}

.custom-table-selectable {
    margin: 5px 0 5px 0;
    box-shadow: 0px 3px 6px #00000029;
    .MuiTable-root {
        width: 100%;
        padding: 5px 0 5px 0;
    }
    .MuiTableCell-root {
        border-bottom: none;
    }
    .MuiTableCell-head {
        color: #000000;
        background-color: #DDDFE3;
        padding: 10px 0 5px 10px;
        line-height: 1;
    }
    .MuiTableCell-body {
        font-size: inherit;
        padding: 15px 0 15px 15px;
        line-height: 1;
    }
    .MuiTableRow-root:hover {
        cursor: pointer;
    }
    .MuiTableCell-root:last-child {
        padding-right: 10px;
    }
}

.check-box {
    &.MuiIconButton-colorSecondary.Mui-checked {
        color: #3AB77D;
    }
}

.footer {
    background-color: #363636AA;
    height: 56px;
    position: fixed;
    bottom: 0;
    left: 85px;
    right: 0;
    text-align: right;
    padding: 8px 25px;
    z-index: 2;

    .button {
        font-family: Usual;
        font-size: 14px;
        font-weight: 500;
        color: #FFFFFF;
        padding: 0 28px;
        margin-left: 25px;
        height: 100%;
        &.MuiButtonBase-root {
            background-color: #3AB77D;
        }
        &.MuiButtonBase-root:hover {
            background-color: #008650;
        }
    }
    .button-transparent {
        line-height: 1;
        padding: 0 28px;
        margin-left: 25px;
        height: 100%;
        border-color: #3AB77D;
        border-width: 2px;
        background-color: #3AB77D62;
        &.MuiButtonBase-root {
            border-color: #3AB77D;
            background-color: #3AB77D62;
        }
        &.MuiButtonBase-root:hover {
            background-color: #00865062;
        }
        .text {
            font-family: Usual;
            font-size: 14px;
            font-weight: 500;
            color: #FFFFFF;
        }
        .small-text {
            font-family: Usual;
            font-size: 8px;
            font-weight: 500;
            color: #FFFFFF;
        }
    }
    .button-error {
        font-family: Usual;
        font-size: 14px;
        font-weight: 500;
        color: #FFFFFF;
        padding: 0 28px;
        margin-left: 25px;
        height: 100%;
        &.MuiButtonBase-root {
            
            background-color: #FD5451;
        }
        &.MuiButtonBase-root:hover {
            background-color: #DB3230;
        }
    }
    .button-neutral {
        font-family: Usual;
        font-size: 14px;
        font-weight: 500;
        color: #272E4C;
        padding: 0 28px;
        margin-left: 25px;
        height: 100%;
        &.MuiButtonBase-root {
            background-color: #F5F7FB;
        }
        &.MuiButtonBase-root:hover {
            background-color: #CCCCCC;
        }
    }
}

.number-item {
    background-color: #9EA3AC;
    color: #FFFFFF;
    font-weight: 600;
    width: 17px;
    height: 17px;
    line-height: 17px;
    text-align: center;
    border-radius: 3px;
    left: 50%;
}


.ticket-modal {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    background-color: #F5F7FB;
    width: 300px;
    box-shadow: 0px 3px 6px #00000029;
}

.mini-ticket-modals {
    position: absolute;
    left: 1rem;
    right: 1rem;
    z-index: 2;
    box-shadow: 0px 3px 6px #00000029;
}

.mini-ticket-modal {
    background-color: #FFFFFF;
}
.mini-ticket-modal:hover {
    cursor: pointer;
    background-color: #EEEEEE;
}

.custom-tabs {
    .MuiTab-root {
        line-height: 1;
        font-weight: 400;
        text-align: left;
    }
    .MuiTabs-indicator {
        display: none;
    }
    .MuiTab-textColorInherit {
        opacity: 0.3;
    }
}

